.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.card {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border: none;
  margin-bottom: 20px;
  margin-top: 20px;
}

.card-header {
  font-weight: bold;
}

.card-bar {
  display: flex;
  justify-content: flex-end;
}

.card-bar h4{
  font-size: 20px;
}

.list-group-item{
  cursor: pointer;
}
.result-card {
  text-align: center;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.result-card h4 {
  margin-bottom: 10px;

}

.result-card p {
  margin-bottom: 5px;
}

.restart-button {
  display: block;
  margin: 0 auto;
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.restart-button:hover {
  background-color: #0056b3;
}

.text-success {
  color: #28a745;
}

.text-danger {
  color: #dc3545;
}

.button{
  background-color: green;
}